.App {
  text-align: center;
  color: white;
  min-height: 100vh;
  text-align: center;
}

.App-header {
  font-size: calc(10px + 2vmin);
}

.results-Header {
  text-align: center;
  background-color: #fff;
  color: #000;
  font-weight: bold;
  padding: 10px;
  margin-top: 10px;
}

.results-container {
  font-size: 72px;
  width: 602px;
  margin: 0px auto;
}

.result {
  min-width: 300px;
  float: left;
  height: 174px;
}

.image img {
  width: calc(400px / 3);
  height: calc(518px / 3);
  float: left;
}

.points {
  text-align: center;
  margin: 0;
  top: 20%;
  position: relative;
}

.result img {
  background-color: white;
  border: 1px solid black;
}

.brass {
  background-color: gray;
  color: white;
}

.woodwinds {
  background-color: black;
  color: white;
}

.colorguard {
  background-color: #7c13ba;
  color: white;
}

.percussion {
  background-color: white;
  color: black;
}

.bottom-right {
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}
